.centerText{
  text-align: center;
}
.centerElem{
  margin: auto;
}
.RightAlign{
  float: right;
  margin-right: 20px;
}
.Button{
  width: auto;
  height: 30px;
  font-size: 20px;
  border-radius: 5px;
  display: block;
}
#appName{
  cursor: pointer;
  display: block;
  margin-top: 20px;
  text-decoration: underline 1px;
  background-color: rgba(0, 110, 255, 0.254);
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 10px;
  max-width: 100%;
  margin-bottom: 20px;
}
#title{
  text-align: center;
  margin: auto;
  margin-top: 30px;
  font-size: 18px;
  width: 60%;
  background-color: #f5f5f5;
}
#shortUrl,#changePass{
  margin: auto;
  margin-top: 50px;
  width: max-content;
  background-color: #597e6574;
  padding: 30px;
  border-radius: 10px;
  max-width: 100%;
}
#shortBtn{
  font-size: 15px;
  margin: auto;
  margin-top: 40px;
  border: 1px solid;
  background-color: #c6d1e2d1;
}
#bottomDesc {
  margin: auto;
  margin-top: 50px;
  text-align: center;
  padding: 10px;
  width: 60%;
  background-color: #f5f5f5;
}
#urlInput{
  outline: none;
  border: none;
  border-bottom: 1px solid;
  text-align: center;
  font-size: 18px;
  color:black;
  background-color: #597e6574;
  border-radius: 10px;
  margin-top: 20px;
}
::placeholder {
  text-align: center;
  font-size: 18px;
  letter-spacing: 1px;
  word-spacing: 2px;
  color: #faf6ed;
}
.inputText{
  outline: none;
  border: none;
  border-bottom: 1px solid;
  font-size: 18px;
  color:black;
  background-color: #95ad9cba;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
  margin-top: 20px;
}
.submitBtn{
  font-size: 15px;
  margin: auto;
  margin-top: 40px;
  border: 1px solid;
  background-color: rgb(154, 186, 241);
}
#savedLinks{
  margin: auto;
  padding: 10px;
  margin-top: 30px;
  background-color: #a1bd9e;
  text-align: center;
  width: max-content;
  border-radius: 10px;
}
#mobileTable{
  display: none;
}
body{
  margin-left: 20px;
  margin-right: 20px;
}
textarea{
  background-color: #a1bd9e;
  outline: none;
  border: none;
  resize: vertical;
  color: black;
  height: auto;
}
@media only screen and (max-width: 500px) {
  #savedLinks{
      display: none;
  }
  #mobileTable{
      display: block;
      margin-top: 100px;
  }
  #appName{
      width: 100%;
  }
  #shortUrl{
      max-width: 80%;
  }
  #changePass{
      max-width: 80%;
  }
  .inputText{
      width: 100%;
  }
  textarea {
      box-sizing: border-box;
      height: auto !important;
      overflow: auto;
    }
    
  ::placeholder {
      color: #505768;
      font-size: 13px;
    }
  #title{
      width: 90%;
  }
  #bottomDesc{
      width: 90%;
  }
}
#mobileTable table{
  border: 1px solid;
  margin-top: 20px;
  overflow: hidden;
  max-width: 100%;
  margin: auto;
}
#shortUrl table,#changePass table{
  text-align: right;
}
#shortUrl td,#changePass td{
  vertical-align: bottom;
}
button{
  height: 25px;
  border: none;
  border-radius: 10px;
  color: black;
  background-color: #f5d8d861;
}
.imgBtn{
  cursor: pointer;
}

#btnloader{
  display:none;
  width: 20px;
  height: 20px;
  border: 3px solid;
  border-radius: 50%;
  border-color:#000 transparent #000 #000;
  animation: loader 1.2s linear infinite;
}
@keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
h4,h1{
  text-align: center;
}
#LogoutMsg{
  margin-top: 30px;
}